<template>
  <v-container fluid class="mb-5">
    <v-card class="mb-3">
      <v-toolbar flat color="green" dark>
        <v-btn color="orange" @click="goBack" class="mr-2">Back</v-btn>
        <v-btn color="primary" @click="goToListSantri">Registrasi Santri</v-btn>
        <v-spacer></v-spacer>
        <v-btn color="green" @click="exportToExcel">Ekspor ke Excel</v-btn>
      </v-toolbar>

      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Cari Santri"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
    </v-card>

    <v-data-table :headers="headers" :items="filteredSantris" :search="search" class="elevation-1">
      <!-- Kolom Update -->
      <template v-slot:item.update="props">
        <td>
          <v-btn @click="navigateToprofilesantri(props.item)" color="primary">Update</v-btn>
        </td>        
        <td>
          <v-btn @click="deleteSantri(props.item)" color="secondary">Delete</v-btn>
        </td>
      </template>
      <!-- /Kolom Update -->

      <!-- Kolom Data Santri -->
      <template v-slot:items="props">
        <td>{{ props.item.nama }}</td>
        <td>{{ props.item.kelas }}</td>
        <td>{{ props.item.umur }}</td>
        <td>{{ props.item.jeniskelamin }}</td>
        <td>
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on">
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="deleteSantri(props.item)">
                <v-list-item-title>Delete</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </td>
      </template>
      <!-- /Kolom Data Santri -->
    </v-data-table>
  </v-container>
</template>

<script>
import firebase from "firebase/app";
import "firebase/firestore";
import Swal from "sweetalert2";
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";

export default {
  data() {
    return {
      search: "",
      santris: [],
      filteredSantris: [],
      headers: [
        { text: "Update", value: "update", sortable: false },
        { text: "Nama", value: "nama" },
        { text: "Kelas", value: "kelas" },
        { text: "Umur", value: "umur" },
        { text: "Jenis Kelamin", value: "jeniskelamin" },
      ],
    };
  },
  created() {
    this.getSantriList();
  },
  watch: {
    search(val) {
      this.filteredSantris = this.santris.filter((santri) => {
        return Object.values(santri)
          .some((value) => value.toString().toLowerCase().includes(val.toLowerCase()));
      });
    },
  },
  methods: {
    getSantriList() {
      const db = firebase.firestore();
      db.collection("santris")
        .get()
        .then((querySnapshot) => {
          const santris = [];
          querySnapshot.forEach((doc) => {
            const santri = doc.data();
            santri.id = doc.id;
            santris.push(santri);
          });
          this.santris = santris;
          this.filteredSantris = santris;
        })
        .catch((error) => {
          Swal.fire({
            icon: "error",
            title: "Error",
            text: "Terjadi kesalahan saat mengambil data santri",
          });
          console.error("Error getting santri list:", error);
        });
    },
    goBack() {
        Swal.fire({
          title: 'Are you sure?',
          text: "Do you want to leave this page?",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, go back!'
        }).then((result) => {
          if (result.isConfirmed) {
            this.$router.push("/home");
          }
        });
      },
    navigateToprofilesantri(santri) {
      // Navigasi ke ProfileSantri.vue dengan ID santri
      this.$router.push({ name: "profilesantri", params: { santriId: santri.id } });
    },
    deleteSantri(santri) {
  // Tampilkan konfirmasi penghapusan kepada pengguna
  Swal.fire({
    title: "Konfirmasi",
    text: "Anda yakin ingin menghapus santri ini?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Ya, hapus",
    cancelButtonText: "Batal",
  }).then((result) => {
    if (result.isConfirmed) {
      const db = firebase.firestore();
      const santrisRef = db.collection("santris");

      // Hapus santri dari Firestore berdasarkan ID
      santrisRef
        .doc(santri.id)
        .delete()
        .then(() => {
          Swal.fire({
            icon: "success",
            title: "Sukses",
            text: "Santri berhasil dihapus",
          });

          // Refresh daftar santri setelah penghapusan
          this.getSantriList();
        })
        .catch((error) => {
          // Handle Firebase error
          if (error.code === "permission-denied") {
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Anda tidak memiliki izin untuk menghapus santri",
            });
          } else {
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Terjadi kesalahan saat menghapus santri",
            });
          }
          console.error("Error deleting santri:", error);
        });
    }
  });
},

    exportToExcel() {
      const data = this.filteredSantris.map((santri) => ({
        Nama: santri.nama,
        Kelas: santri.kelas,
        Umur: santri.umur,
        "Jenis Kelamin": santri.jenisKelamin,
      }));

      const worksheet = XLSX.utils.json_to_sheet(data);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Santris");
      const excelBuffer = XLSX.write(workbook, { bookType: "xlsx", type: "array", compression: true });
      const dataBlob = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
      const filename = "santris_data.xlsx";

      saveAs(dataBlob, filename);
    },
    goToListSantri() {
      this.$router.push("/list-santri");
    },
  },
};
</script>

<style scoped>
/* Style tambahan jika diperlukan */
</style>
