<template>
  <div>
    <v-app-bar app class="green" dark>
      <v-btn color="orange" @click="goBack">
          Back
      </v-btn>

      <v-card-title class="headline">
        <img src="/LTQ-HN/favicon.ico" alt="Logo" class="logo-small">
        LTQ-HN Absen Sore
      </v-card-title>
    </v-app-bar>
    <v-container fluid>
      <v-card class="check-out-card">
        <v-card-text>
          <v-row align="center" justify="center">
            <v-col cols="6">

              <!-- Date Time -->
              <v-card class="menu-card">
                <v-row align="center">
                  <v-col cols="12">
                    <h3 class="menu-title">Date Time</h3>
                    <div class="check-out-header">
                      <h1>{{ currentTime }}</h1>
                      <h3>{{ formattedDate }}</h3>
                    </div>
                    <div class="check-out-user">
                      <h3>{{ userData && userData.displayName }}</h3>
                    </div>
                  </v-col>
                </v-row>
              </v-card>

              <!-- Location -->
              <v-card class="menu-card">
                <v-row align="center">
                  <v-col cols="12">
                    <div class="check-out-location">
                      <h4>{{ currentLocation }}</h4>
                      <div ref="mapElement" class="map-element"></div>
                    </div>
                  </v-col>
                </v-row>
              </v-card>

              <!-- Submit Check Out Button -->
              <v-card class="menu-card">
                <v-row align="center">
                  <v-col cols="12">
                    <div class="check-out-actions">
                      <v-btn color="success" @click="submitCheckOut">ABSEN SORE</v-btn>
                    </div>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-container>
  </div>
</template>


<script>
import firebase from "firebase/app";
import "firebase/auth";
import { db, storage } from "@/main";
import Swal from "sweetalert2";
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/LTQ-HN/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/LTQ-HN/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/LTQ-HN/images/marker-shadow.png'),
});

export default {
  name: "CheckOut",
  data() {
    return {
      userData: null,
      formattedDate: "",
      currentTime: "",
      currentLocation: "",
      photoDataUrl: null,
      capturedFileName: ""
    };
  },
  async created() {
    const user = firebase.auth().currentUser;
    if (user) {
      await this.setUserData();
      this.getCurrentDateTime();
      this.getCurrentLocation();
    } else {
      console.error("User not logged in.");
      this.$router.push("/home");
    }
  },

  methods: {
    goBack() {
      this.$router.push("/home");
    },
    async setUserData() {
      const user = firebase.auth().currentUser;
      if (user) {
        const registrationSnapshot = await db
          .collection("registrations")
          .where("email", "==", user.email)
          .get();
        if (!registrationSnapshot.empty) {
          // Hanya mengambil data pertama jika ada multiple dokumen dengan email yang sama
          const registrationData = registrationSnapshot.docs[0].data();
          this.userData = registrationData;
        } else {
          console.error("User data not found.");
        }
      } else {
        console.error("User not logged in.");
        this.$router.push("/login");
      }
    },
    getCurrentDateTime() {
      const currentDate = new Date();
      this.formattedDate = currentDate.toLocaleDateString("en-US", {
        weekday: "long",
        month: "long",
        day: "numeric",
        year: "numeric",
      });
      this.currentTime = currentDate.toLocaleTimeString("en-US", {
        hour: "numeric",
        minute: "numeric",
        hour12: true,
      });
    },
    getCurrentLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const latitude = position.coords.latitude;
            const longitude = position.coords.longitude;
            this.currentLocation = `Latitude: ${latitude}, Longitude: ${longitude}`;

            this.showMap(latitude, longitude);
          },
          (error) => {
            console.error("Error getting current location:", error);
          }
        );
      } else {
        console.error("Geolocation is not supported by this browser.");
      }
    },

    submitCheckOut() {
      const user = firebase.auth().currentUser;
      if (user && this.userData && this.userData.displayName) {
        const checkOutData = {
          displayName: this.userData.displayName,
          fileName: this.capturedFileName,
          location: this.currentLocation,
          timestamp: firebase.firestore.Timestamp.now(),
        };

        db.collection("check-outs")
          .add(checkOutData)
          .then(() => {
            if (this.photoDataUrl) {
              this.uploadPhoto(this.photoDataUrl, this.capturedFileName);
            }
            Swal.fire({
              icon: "success",
              title: "Success",
              text: "Absensi Sore Berhasil",
            }).then(() => {
              this.goBack();
            });
          })
          .catch((error) => {
            console.error("Error submitting check-out:", error);
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Gagal Melakukan Absen Sore",
            });
          });
      } else {
        console.error("User data is missing or incomplete.");
        Swal.fire({
          icon: "error",
          title: "Error",
          text: "Failed to submit check-out: User data is missing or incomplete",
        });
      }
    },
    showMap(latitude, longitude) {
      const mapElement = this.$refs.mapElement;

      // Buat objek peta dengan latitude dan longitude yang diberikan
      const map = L.map(mapElement).setView([latitude, longitude], 13);

      // Tambahkan layer peta dari OpenStreetMap
      L.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(map);

      // Tambahkan penanda pada lokasi yang diberikan
      L.marker([latitude, longitude]).addTo(map)
        .bindPopup('Lokasi Check Out')
        .openPopup();
    },
  },
};
</script>

<style scoped>
.check-in-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
}

.check-in-header {
  text-align: center;
  margin-bottom: 24px;
}

.check-in-user {
  text-align: center;
  margin-bottom: 16px;
}

.check-out-photo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 16px;
}

.photo-preview img {
  max-width: 300px;
  max-height: 300px;
}

.video-preview {
  width: 100%;
  max-width: 300px;
  height: auto;
}

.check-out-location {
  text-align: center;
  margin-bottom: 16px;
}

.check-out-actions {
  text-align: center;
}

.logo-small {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

.map-element {
  width: 100%;
  height: 300px;
  margin-top: 16px;
}

.check-out-card {
  margin-top: 24px;
}

.menu-card {
  margin-bottom: 24px;
  padding: 16px;
}

.menu-icon {
  font-size: 24px;
}

.menu-title {
  margin-top: 8px;
  margin-bottom: 16px;
  text-align: center;
}

.check-out-header {
  margin-bottom: 8px;
  text-align: center;
}

.check-out-user {
  margin-bottom: 16px;
  text-align: center;
}

.check-out-actions {
  text-align: center;
  margin-top: 16px;
}
</style>
