import Vue from 'vue'
import App from './App.vue'
import VueMeta from 'vue-meta';
import vuetify from './plugins/vuetify'
import router from "./router";
import mixin from "./mixin";
import store from "./store";
import ToastPlugin from 'vue-toast-notification';
import 'vue-toast-notification/dist/theme-sugar.css';
import firebase from "firebase";
import "firebase/firestore"; // Import library Firestore
import "firebase/storage";


// import vueNiceScrollbar from 'vue-nice-scrollbar';
Vue.use(ToastPlugin);
Vue.use(VueMeta);

// Vue.use(vueNiceScrollbar);

Vue.config.productionTip = false
const firebaseConfig = {
  apiKey: "AIzaSyAdqS6loC6Ry3USA2q8ji8vlmunm85tZt0",
  authDomain: "dovepin-project-5faf2.firebaseapp.com",
  projectId: "dovepin-project-5faf2",
  storageBucket: "dovepin-project-5faf2.appspot.com",
  messagingSenderId: "740700379866",
  appId: "1:740700379866:web:36faa076b5487512b0a3e3"
};


firebase.initializeApp(firebaseConfig);
// Inisialisasi instance Firestore
const db = firebase.firestore();
// Export instance Firestore agar dapat digunakan di komponen lain
export { db };
const storage = firebase.storage();
export {storage};

// mixin
Vue.mixin(mixin);

new Vue({
  router,
  vuetify,
  store,
  render: (h) => h(App),
}).$mount('#app');