<template>
    <v-container fluid class="mb-5">
        <v-app-bar app class="green" dark>
            <v-card-title class="headline">
                <v-btn color="orange" @click="goBack">
                Back
                </v-btn>
                <img src="/LTQ-HN/favicon.ico" alt="Logo" class="logo-small">
                Haen Dashboard
            </v-card-title>
            <v-spacer></v-spacer>
        </v-app-bar>

        <v-card class="mb-3">
            <v-card-title>
                <v-row>
                    <v-col cols="12" sm="6" md="4">
                        <v-menu v-model="menu" :close-on-content-click="false" offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field v-model="startDate" label="Start Date" readonly v-bind="attrs"
                                    v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="startDate" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="applyFilter">OK</v-btn>
                            </v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                        <v-menu v-model="menu2" :close-on-content-click="false" offset-y>
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field v-model="endDate" label="End Date" readonly v-bind="attrs"
                                    v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="endDate" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu2 = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="applyFilter">OK</v-btn>
                            </v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="6" sm="6" md="4">
                        <v-btn class="mb-2 pl-4" color="primary" @click="applyFilter">Apply Filter</v-btn>
                    </v-col>
                    <v-col cols="6">
                        <v-btn class="mb-2" color="" dark @click="clearFilter">Clear Filter</v-btn>
                    </v-col>
                </v-row>
                <v-btn class="mb-2" color="green" dark @click="exportToExcel">Export Excel</v-btn>
            </v-card-title>
        </v-card>

        <v-row>
            <v-col cols="12" sm="6">
                <v-card>
                    <v-card-text>
                        <v-data-table :headers="headersMasuk" :items="filteredAbsenMasuk" item-key="id" hide-default-footer
                            class="elevation-1">
                            <template v-slot:top>
                                <v-toolbar flat>
                                    <v-toolbar-title>Pagi</v-toolbar-title>
                                    <v-spacer></v-spacer>
                                </v-toolbar>
                            </template>
                        </v-data-table>
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col cols="12" sm="6">
                <v-card>
                    <v-card-text>
                        <v-data-table :headers="headersKeluar" :items="filteredAbsenKeluar" item-key="id"
                            hide-default-footer class="elevation-1">
                            <template v-slot:top>
                                <v-toolbar flat>
                                    <v-toolbar-title>Sore</v-toolbar-title>
                                    <v-spacer></v-spacer>
                                </v-toolbar>
                            </template>
                        </v-data-table>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
  
<script>
import firebase from "firebase/app";
import "firebase/firestore";
import Swal from "sweetalert2";
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";

export default {
    name: "ListAbsenView",
    data() {
        return {
            startDate: null,
            endDate: null,
            menu: false,
            menu2: false,
            startDate2: null,
            endDate2: null,
            menu3: false,
            menu4: false,
            absenListMasuk: [],
            absenListKeluar: [],
            filteredAbsenMasuk: [],
            filteredAbsenKeluar: [],
            headersMasuk: [
                { text: "No", value: "no" },
                { text: "Nama", value: "displayName" },
                { text: "Pagi", value: "masuk" },
            ],
            headersKeluar: [
                { text: "No", value: "no" },
                { text: "Nama ", value: "displayName" },
                { text: "Sore", value: "keluar" },
            ],
        };
    },
    created() {
        this.getAbsenList();
    },
    methods: {
        getAbsenList() {
            const db = firebase.firestore();
            const checkInsRef = db
                .collection("check-ins")
                .orderBy("timestamp", "desc");
            const checkOutsRef = db
                .collection("check-outs")
                .orderBy("timestamp", "desc");

            Promise.all([checkInsRef.get(), checkOutsRef.get()])
                .then((results) => {
                    const absenListMasuk = results[0].docs.map((doc, index) => {
                        const absen = doc.data();
                        return {
                            ...absen,
                            no: index + 1,
                            masuk: absen.timestamp.toDate().toLocaleString(),
                        };
                    });

                    const absenListKeluar = results[1].docs.map((doc, index) => {
                        const absen = doc.data();
                        return {
                            ...absen,
                            no: index + 1,
                            keluar: absen.timestamp.toDate().toLocaleString(),
                        };
                    });

                    this.absenListMasuk = absenListMasuk;
                    this.absenListKeluar = absenListKeluar;
                    this.filteredAbsenMasuk = absenListMasuk;
                    this.filteredAbsenKeluar = absenListKeluar;
                })
                .catch((error) => {
                    Swal.fire({
                        icon: "error",
                        title: "Error",
                        text: "Terjadi kesalahan saat mengambil data absensi",
                    });
                    console.error("Error getting absen list:", error);
                });
        },
        exportToExcel() {
            // const data = this.absenListMasuk.map((absen) => ({
            //     No: absen.no,
            //     "Nama (displayName)": absen.displayName,
            //     Masuk: absen.masuk ? new Date(absen.masuk).toLocaleString() : "",
            //     Keluar: "",
            // }));

            // this.absenListKeluar.forEach((absen, index) => {
            //     const dataItem = data[index];
            //     if (dataItem) {
            //         dataItem.Keluar = absen.keluar ? new Date(absen.keluar).toLocaleString() : "";
            //     }
            // });
           
            const data = this.filteredAbsenMasuk.map((absen) => ({
                No: absen.no,
                "Nama (Masuk)": absen.displayName,
                Masuk: absen.masuk ? new Date(absen.masuk).toLocaleString() : "",
                " " : " ",
                "Nama (Keluar)" : "",
                Keluar: "",
            }));

            this.filteredAbsenKeluar.forEach((absen, index) => {
                const dataItem = data[index];
                if (dataItem) {
                    if(absen.keluar){
                        dataItem.Keluar = new Date(absen.keluar).toLocaleString()
                        dataItem["Nama (Keluar)"] = absen.displayName
                    } else {
                        dataItem.Keluar = "";
                    }
                }
            });


            console.log(data)
            const worksheet = XLSX.utils.json_to_sheet(data);
            const workbook = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(workbook, worksheet, "Absensi");
            const excelBuffer = XLSX.write(workbook, {
                bookType: "xlsx",
                type: "array",
            });

            const filename = "absen_data.xlsx";
            const file = new Blob([excelBuffer], {
                type:
                    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
            });
            saveAs(file, filename);
        },
        applyFilter() {
            if (this.startDate && this.endDate) {
                const filteredListMasuk = this.absenListMasuk.filter((absen) => {
                    const startDate = new Date(this.startDate).setUTCHours(0,0,0,0);
                    const endDate = new Date(this.endDate).setUTCHours(23,59,59,999);
                    const timestamp = new Date(absen.masuk);
                    
                    return timestamp >= startDate && timestamp <= endDate;
                });
                
                const filteredListKeluar = this.absenListKeluar.filter((absen) => {
                    const startDate = new Date(this.startDate).setUTCHours(0,0,0,0);
                    const endDate = new Date(this.endDate).setUTCHours(23,59,59,999);
                    const timestamp = new Date(absen.keluar);
                    
    
                    return timestamp >= startDate && timestamp <= endDate;
                });
                
                this.filteredAbsenMasuk = filteredListMasuk;
                this.filteredAbsenKeluar = filteredListKeluar;
            } else {
                this.filteredAbsenMasuk = this.absenListMasuk;
                this.filteredAbsenKeluar = this.absenListKeluar;
            }
        },
        clearFilter() {
            this.startDate = null;
            this.endDate = null;
            this.startDate2 = null;
            this.endDate2 = null;
            this.filteredAbsenMasuk = this.absenListMasuk;
            this.filteredAbsenKeluar = this.absenListKeluar;
        },
        goBack() {
            this.$router.push("/dashboard");
        },
    },
};
</script>
  
<style scoped>
.v-data-table tbody tr td {
    white-space: nowrap;
}

.logo-small {
    width: 50px;
    height: 50px;
    display: block;
    margin: 0 auto;
}
</style>
  