<template>
    <v-container>
      <v-card class="pa-5">
        <v-btn color="orange" dark @click="goBack" class="mr-2">Back</v-btn>
      <v-btn color="primary" @click="saveSantri">Save</v-btn>
        <!-- Buttons untuk toggle form -->
        <v-row justify="end">
                <v-col cols="auto">
          <v-btn color="secondary" @click="showForm = 'registrasi'" class="mr-2">Form Registrasi</v-btn>
        </v-col>
        <v-col cols="auto">
          <v-btn color="secondary" @click="showForm = 'hafalan'">Form Laporan Hafalan</v-btn>
        </v-col>
      </v-row>
        <!-- Form Registrasi Santri -->
        <div v-if="showForm === 'registrasi'">
          <v-form ref="form" v-model="valid">
            <!-- Form fields untuk registrasi santri -->
            <v-text-field label="Nama Santri" v-model="santri.nama" :rules="nameRules" required></v-text-field>
            <v-text-field label="Kelas" v-model="santri.kelas" required></v-text-field>
            <v-select label="Jenis Kelamin" :items="['Laki-Laki', 'Perempuan']" v-model="santri.jeniskelamin"></v-select>
            <v-text-field label="Umur" v-model="santri.umur" type="number" :rules="ageRules" required></v-text-field>
            <v-text-field label="Alamat" v-model="santri.alamat" required></v-text-field>
            <v-text-field label="No. Telp" v-model="santri.noTelp" required></v-text-field>
            <v-file-input label="Foto Santri" @change="uploadFoto" accept="image/*"></v-file-input>
          </v-form>
        </div>
  
        <!-- Form Laporan Hafalan -->
        <div v-if="showForm === 'hafalan'">
          <div v-for="(item, index) in laporanHafalanItems" :key="index">
            <v-row>
              <v-col cols="12" sm="2">
                <v-text-field label="Tanggal" type="date" v-model="item.tanggal" :value="currentDate" required></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <v-text-field label="Surat Hafalan" v-model="item.surat" required></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <v-text-field label="Ayat / Halaman" v-model="item.ayatHalaman" required></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <v-select label="Penilaian" :items="penilaianItems" v-model="item.penilaian" required></v-select>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field label="Keterangan" v-model="item.keterangan"></v-text-field>
              </v-col>
              <v-col cols="12" sm="1">
                <v-btn icon color="red" @click="deleteLaporanHafalanItem(index)">
                    <v-icon>mdi-delete</v-icon>
                </v-btn>
                </v-col>
            </v-row>
          </div>
          <v-btn @click="addLaporanHafalanItem">Tambah Kolom</v-btn>
        </div>
      </v-card>
    </v-container>
  </template>
  
  <script>
  import Swal from 'sweetalert2';
  import firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/firestore';
  import 'firebase/storage';
  
  export default {
    data() {
      return {
        valid: true,
        showForm: 'registrasi',
        santri: {
          nama: '',
          kelas:'',
          jeniskelamin:'',
          umur: '',
          alamat: '',
          noTelp: '',
          foto: ''
        },
        laporanHafalanItems: [
          { tanggal: new Date().toISOString().substr(0, 10), surat: '', ayatHalaman: '', penilaian: '', keterangan: '' }
        ],
        penilaianItems: ['Maqbul', 'Jayyid', 'Jayyid Jiddan','Mumtaz'],
        nameRules: [v => !!v || 'Nama diperlukan'],
        ageRules: [v => !!v || 'Umur diperlukan'],
        currentDate: new Date().toISOString().substr(0, 10)
      };
    },
    methods: {
      goBack() {
        Swal.fire({
          title: 'Are you sure?',
          text: "Do you want to leave this page?",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, go back!'
        }).then((result) => {
          if (result.isConfirmed) {
            this.$router.push("/hafalan");
          }
        });
      },
      deleteLaporanHafalanItem(index) {
      this.laporanHafalanItems.splice(index, 1);
    },
    async saveSantri() {
    // Validasi form santri
    if (this.$refs.form && this.$refs.form.validate()) {
      try {
        // Proses penyimpanan data santri
        this.santri.nomorSantri = this.generateUniqueId();
        const santriRef = await firebase.firestore().collection("santris").add(this.santri);
        const santriId = santriRef.id;

        // Proses penyimpanan laporan hafalan
        for (const item of this.laporanHafalanItems) {
          await firebase.firestore().collection("hafalans").add({
            ...item,
            nomorSantri: this.santri.nomorSantri,
            santriId: santriId
          });
        }

        Swal.fire('Saved!', 'All data has been saved.', 'success');
        this.resetForm();
      } catch (error) {
        console.error("Error saving data:", error);
        Swal.fire('Error!', 'Failed to save data.', 'error');
      }
    } else {
      // Tampilkan pesan error jika validasi gagal
      Swal.fire('Error!', 'Please fill in the registration form correctly.', 'error');
    }
  },
      generateUniqueId() {
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890';
        let result = 'SAN';
        const charactersLength = characters.length;
        for (let i = 0; i < 5; i++) {
          result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
      },
      async uploadFoto(file) {
        if (!file) return;
        try {
          const fileName = this.santri.nama.replace(/\s+/g, '_') + '_' + file.name;
          const storageRef = firebase.storage().ref().child(fileName);
          const snapshot = await storageRef.put(file);
          this.santri.foto = await snapshot.ref.getDownloadURL();
        } catch (error) {
          console.error("Error uploading photo:", error);
          Swal.fire('Error!', 'Failed to upload photo.', 'error');
        }
      },
      resetForm() {
        this.$refs.form.reset();
        this.santri = {
          nama: '',
          kelas:'',
          jeniskelamin:'',
          umur: '',
          alamat: '',
          noTelp: '',
          foto: ''
        };
        this.laporanHafalanItems = [{ tanggal: new Date().toISOString().substr(0, 10), surat: '', ayatHalaman: '', penilaian: '', keterangan: '' }];
      },
      addLaporanHafalanItem() {
        this.laporanHafalanItems.push({ tanggal: new Date().toISOString().substr(0, 10), surat: '', ayatHalaman: '', penilaian: '', keterangan: '' });
      }
    }
  };
  </script>
  
  <style>
  /* CSS styles if needed */
  </style>
  