<template>
  <v-container class="mb-5">
    <v-app-bar app class="green" dark>
      <v-card-title class="headline">
        <img src="/LTQ-HN/favicon.ico" alt="Logo" class="logo-small">
        LTQ-HN
      </v-card-title>
      <v-toolbar-title class="ml-8 clickable" @click="navigateHome">Home</v-toolbar-title>
      <v-toolbar-title class="ml-8 clickable" @click="navigateDashboard" v-if="isAdmin">Dashboard</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-title v-if="userData">
        Selamat Datang, {{ userData.displayName }}
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn class="mr-8" color="error" dark @click="logout">Log Out</v-btn>
    </v-app-bar>
    <v-row v-if="login">
      <v-col cols="12" sm="12" md="12" lg="4" v-for="menu in menus" :key="menu.title">
        <v-card class="menu-card" @click="navigate(menu.route)">
          <v-img :src="menu.icon" class="menu-icon"></v-img>
          <v-card-title class="menu-title">{{ menu.title }}</v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="!login">
      <!-- Tampilan jika tidak login -->
    </v-row>
  </v-container>
</template>

<script>
import Swal from "sweetalert2";
import firebase from "firebase/app";
import "firebase/auth";
import { db } from "@/main"; // Sesuaikan dengan path import database Anda


export default {
  name: "Home",
  data() {
    return {
      userData: null, // Untuk menyimpan data pengguna saat ini
      login: true,
      isAdmin : false,
      menus: [
        {
          title: "Absen Pagi",
          icon: "/LTQ-HN/images/CheckIn.jpg",
          route: "/checkin"
        },
        {
          title: "Absen Sore",
          icon: "/LTQ-HN/images/CheckOut.jpg",
          route: "/checkout"
        },
        {
          title: "Report Hafalan",
          icon: "/LTQ-HN/images/AllList.jpg",
          route: "/hafalan"
        },
      ]
    };
  },
  methods: {
    navigate(route) {
      // Routing ke tujuan
      this.$router.push(route);
    },
    navigateDashboard() {
      this.$router.push("/dashboard");
    },
    navigateHome() {
      // Routing ke Home
      this.$router.push("/home");
    },
    logout() {
      Swal.fire({
        title: "Konfirmasi",
        text: "Anda yakin ingin keluar?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Keluar",
        cancelButtonText: "Batal"
      }).then(result => {
        if (result.isConfirmed) {
          // Log out dan routing ke LoginView
          // Clear session, hapus token, dll
          localStorage.clear()
          this.$router.push("/login");
        }
      });
    },
    async fetchUserData() {
      const user = firebase.auth().currentUser;
      if (user) {
        const snapshot = await db.collection("registrations").where("email", "==", user.email).get();
        if (!snapshot.empty) {
          this.userData = snapshot.docs[0].data();
        }
      }
    }
  },
  
  created() {
  this.fetchUserData();

  // Mengatur isAdmin berdasarkan role yang tersimpan di localStorage
  this.isAdmin = localStorage.role === "Admin";
}

  
}
</script>

<style scoped>
.menu-card {
  cursor: pointer;
  text-align: center;
  padding: 16px;
  margin-bottom: 16px;
}

.menu-icon {
  height: 200px;
}

.menu-title {
  margin-top: 16px;
}

.logo-small {
  width: 50px;
  /* Ubah ukuran sesuai kebutuhan */
  height: 50px;
  /* Ubah ukuran sesuai kebutuhan */
  display: block;
  margin: 0 auto;
}

.blue {
  background-color: blue;
  color: white;
}

.clickable {
  cursor: pointer;
}
</style>
