<template>
  <v-container fluid>
    <v-app-bar app class="green" dark>
      <v-card-title class="headline">
        <img src="/LTQ-HN/favicon.ico" alt="Logo" class="logo-small">
        LTQ-HN
      </v-card-title>
      <v-spacer></v-spacer>
      <v-btn color="orange" @click="goBack">
          Back
        </v-btn>
    </v-app-bar>
    <v-container>
  <v-row>
    <!-- Kolom Menu Registrasi dengan gambar -->
    <v-col cols="12" sm="4">
      <v-card class="menu-card">
        <v-img 
          src="/LTQ-HN/images/Register.jpg" 
          height="200px" 
        >
        </v-img>
        <v-card-text>
          <v-btn color="green" dark @click="goToRegistrasi">Registrasi</v-btn>
        </v-card-text>
      </v-card>
    </v-col>

    <!-- Kolom Menu View Karyawan dengan gambar -->
    <v-col cols="12" sm="4">
      <v-card class="menu-card">
        <v-img 
          src="/LTQ-HN/images/profile.jpg" 
          height="200px" 
        >
        </v-img>
        <v-card-text>
          <v-btn color="green" dark @click="goToListKaryawan">Daftar Pembimbing</v-btn>
        </v-card-text>
      </v-card>
    </v-col>

    <!-- Kolom Menu View Absen dengan gambar -->
    <v-col cols="12" sm="4">
      <v-card class="menu-card">
        <v-img 
          src="/LTQ-HN/images/ListAbsen.jpg" 
          height="200px" 
        >
        </v-img>
        <v-card-text>
          <v-btn color="green" dark @click="goToListAbsen">List Absen</v-btn>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</v-container>

  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
import Swal from 'sweetalert2';

export default {
  name: 'DashboardView',
  computed: {
    ...mapGetters(['getUserRole']),
  },
  metaInfo() {
    return {
      meta: [
        {
          name: 'requiresAuth', // Nama meta field
          content: true, // Nilai meta field, bisa diganti dengan kondisi sesuai kebutuhan
        },
        {
          name: 'requiredRole', // Nama meta field
          content: 'admin', // Nilai meta field, bisa diganti dengan kondisi sesuai kebutuhan
        },
      ],
    };
  },
  methods: {
    checkUserRole() {
      const userRole = this.getUserRole();
      if (userRole !== 'admin') {
        this.$router.push('/home');
        Swal.fire({
          icon: 'warning',
          title: 'Akses Ditolak',
          text: 'Anda tidak memiliki akses ke halaman ini.',
          confirmButtonText: 'OK',
        });
      }
    },

    goBack() {
      this.$router.push('/home');
    },
    goToListKaryawan() {
      this.$router.push('/list-karyawan');
    },
    goToListAbsen() {
      this.$router.push('/list-absen');
    },
    goToListCuti() {
      this.$router.push('/list-cuti');
    },
    goToRegistrasi() {
      this.$router.push('/register');
    },
  },
};
</script>

<style scoped>
.logo-small {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

.menu-card {
  cursor: pointer;
  text-align: center;
  padding: 16px;
  margin-bottom: 16px;
}

.headline {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}
</style>
