<template>
  <v-container fluid>
    <v-app-bar app class="green" dark>
      <v-card-title class="headline">
        <v-btn color="orange" @click="goBack">
          Back
        </v-btn>
        <img src="/LTQ-HN/favicon.ico" alt="Logo" class="logo-small">
        LTQ-HN
      </v-card-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-row justify="center">
      <v-col cols="12" sm="8" md="6" lg="4">
        <v-card class="register-card">
          <v-card-title class="headline">Register</v-card-title>
          <v-card-text>
            <v-form @submit.prevent="register">
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.email" label="E-mail" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.password" label="Password" type="password" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.displayName" label="Nama" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-select v-model="input.jenisKelamin" label="Jenis Kelamin"
                      :items="['-- Pilih Jenis Kelamin --', 'Laki-Laki', 'Perempuan']" :rules="jenisKelaminRules"
                      required></v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.tanggalLahir" label="Tanggal Lahir" type="date" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.noTelp" label="No. Telp" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-select v-model="input.jabatan" label="Jabatan"
                      :items="['-- Pilih Jabatan --', 'Admin','Ustad']" :rules="jabatanRules"
                      required></v-select>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-text-field v-model="input.alamat" label="Alamat" required></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-file-input v-model="input.foto" label="Foto" accept="image/*"></v-file-input>
                  </v-col>
                </v-row>
                <v-row class="text-center">
                  <v-col cols="12">
                    <v-btn class="mx-2" color="red" dark @click="cancel">Cancel</v-btn>
                    <v-btn class="mx-2" color="primary" type="submit">Register</v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";
import { db, storage } from "@/main";
import Swal from "sweetalert2";
import { mapMutations } from 'vuex';

export default {
  name: "RegisterView",
  data() {
    return {
      input: {
        email: "",
        password: "",
        displayName: "",
        jenisKelamin: "",
        tanggalLahir: "",
        noTelp: "",
        jabatan: "",
        alamat: "",
        foto: null,
      },
      jenisKelaminRules: [
        (v) => !!v || "Jenis kelamin harus dipilih",
        (v) => v !== "-- Pilih Jenis Kelamin --" || "Jenis kelamin harus dipilih",
      ],
      jabatanRules: [
        (v) => !!v || "Jabatan harus dipilih",
        (v) => v !== "-- Pilih Jabatan --" || "Jabatan harus dipilih",
      ],
    };
  },
  methods: {
    ...mapMutations(['setUserRole']),
    async uploadFoto(file) {
      try {
        const email = this.input.email;
        const fileName = email + '_' + file.name;

        const storageRef = storage.ref().child(fileName);
        const snapshot = await storageRef.put(file);
        const downloadURL = await snapshot.ref.getDownloadURL();

        this.input.foto = downloadURL;
      } catch (error) {
        console.error("Error uploading photo:", error);
        // Handle error accordingly
      }
    },
    
    goBack() {
      this.$router.push('/dashboard');
    },
    async register() {
      if (
        this.input.email &&
        this.input.password &&
        this.input.displayName &&
        this.input.jenisKelamin &&
        this.input.tanggalLahir &&
        this.input.noTelp &&
        this.input.jabatan &&
        this.input.alamat &&
        this.input.foto
      ) {
        try {
          const { user } = await firebase
            .auth()
            .createUserWithEmailAndPassword(this.input.email, this.input.password);
          await this.uploadFoto(this.input.foto);

          const registrationData = {
            email: this.input.email,
            password: this.input.password,
            displayName: this.input.displayName,
            jenisKelamin: this.input.jenisKelamin,
            tanggalLahir: this.input.tanggalLahir,
            noTelp: this.input.noTelp,
            jabatan: this.input.jabatan,
            alamat: this.input.alamat,
            foto: this.input.foto,
          };

          await db.collection("registrations").add(registrationData);

          await user.updateProfile({
            displayName: this.input.displayName.replace(/\s/g, "_"),
          });

          await this.setRoleBasedOnJabatan(this.input.jabatan);

          this.input = {
            email: "",
            password: "",
            displayName: "",
            jenisKelamin: "",
            tanggalLahir: "",
            noTelp: "",
            jabatan: "",
            alamat: "",
            foto: null,
          };

          Swal.fire({
            icon: "success",
            title: "Registration successful. Please login.",
            showConfirmButton: false,
            timer: 2000
          });

          this.$router.push("/login");
        } catch (error) {
          console.error("Registration error:", error.code, error.message);
          // Handle error accordingly
        }
      } else {
        Swal.fire("Please fill in all fields.");
      }
    },
    async setRoleBasedOnJabatan(jabatan) {
      try {
        const email = this.input.email;

        if (jabatan === "Admin") {
          await db.collection("roles").doc(email).set({ role: "admin" });
          this.setUserRole('admin');
        } else if (jabatan === "Ustad") {
          await db.collection("roles").doc(email).set({ role: "usatad" });
          this.setUserRole('ustad');
        }
      } catch (error) {
        console.error("Error setting role:", error);
        // Tampilkan pesan kesalahan menggunakan SweetAlert2
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: 'Terjadi kesalahan saat menyetel peran!',
          footer: 'Silakan coba lagi atau hubungi dukungan jika masalah berlanjut.'
        });
        // Anda juga dapat menambahkan lebih banyak penanganan kesalahan di sini jika diperlukan
      }
    },
    cancel() {
      this.input = {
        username: "",
        password: "",
        displayName: "",
        jenisKelamin: "",
        tanggalLahir: "",
        noTelp: "",
        jabatan: "",
        alamat: "",
        foto: null,
      };
      this.$router.push("/dashboard");
    },
  },
};
</script>

<style scoped>
.register-card {
  margin-top: 32px;
  padding: 16px;
}

.register-btn {
  margin: 8px;
}
.logo-small {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

</style>

